'use client';

import Image from 'next/image';
import { Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, Phone, Mail, MapPin } from 'lucide-react';
import { Button } from '@/components/ui/button';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { Supplier } from '@/types';

interface SupplierTabelProps {
    loading: boolean;
    suppliers: Supplier[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (supplier: Supplier) => void;
    onDelete: (id: number) => void;
}

export default function SupplierTabel({
    loading,
    suppliers,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: SupplierTabelProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;

    return (
        <div className="rounded-md border bg-white shadow-sm">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[50px] text-center">No</TableHead>
                        <TableHead className="w-[80px]">Logo</TableHead>
                        <TableHead>Nama Supplier</TableHead>
                        <TableHead>Kontak</TableHead>
                        <TableHead className="hidden md:table-cell">Alamat</TableHead>
                        <TableHead className="text-right">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={6} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat...
                            </TableCell>
                        </TableRow>
                    ) : suppliers.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={6} className="h-32 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-8 w-8 text-gray-300 mb-2" />
                                    <p>Tidak ada supplier yang cocok.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        suppliers.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50">
                                <TableCell className="text-center text-gray-500">
                                    {(currentPage - 1) * itemsPerPage + index + 1}
                                </TableCell>
                                <TableCell>
                                    <div className="relative h-10 w-10 rounded-full overflow-hidden bg-gray-100 border">
                                        {item.foto_url ? (
                                            <Image 
                                                src={item.foto_url} 
                                                alt={item.nama_supplier} 
                                                fill 
                                                className="object-cover"
                                                unoptimized
                                                onError={(e) => (e.target as HTMLElement).style.display = 'none'}
                                            />
                                        ) : <div className="w-full h-full flex items-center justify-center text-xs font-bold">{item.nama_supplier.charAt(0)}</div>}
                                    </div>
                                </TableCell>
                                <TableCell>
                                    <div className="font-medium text-gray-900">{item.nama_supplier}</div>
                                    <div className="text-xs text-gray-500">{item.keterangan || '-'}</div>
                                </TableCell>
                                <TableCell>
                                    <div className="flex flex-col gap-1 text-sm">
                                        <div className="flex items-center text-gray-700">
                                            <Phone className="w-3 h-3 mr-1" /> {item.nomor_telepon}
                                        </div>
                                        {item.kontak_person && (
                                            <div className="text-xs text-gray-500">CP: {item.kontak_person}</div>
                                        )}
                                    </div>
                                </TableCell>
                                <TableCell className="hidden md:table-cell text-sm text-gray-600 max-w-[200px] truncate">
                                    {item.alamat || '-'}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    <Button variant="ghost" size="icon" onClick={() => onEdit(item)}>
                                        <Pencil className="h-4 w-4 text-blue-600" />
                                    </Button>
                                    <Button variant="ghost" size="icon" onClick={() => onDelete(item.id)}>
                                        <Trash2 className="h-4 w-4 text-red-600" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>

            {/* Pagination */}
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t">
                    <div className="text-sm text-gray-500">
                         Total: {totalData} Supplier
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage - 1)} disabled={currentPage === 1}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Hal {currentPage} / {totalPages}</span>
                        <Button variant="outline" size="sm" onClick={() => onPageChange(currentPage + 1)} disabled={currentPage === totalPages}>
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}