'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Supplier } from '@/types';

import SupplierFilters from '@/components/admin/supplier/SupplierFilter';
import SupplierTable from '@/components/admin/supplier/SupplierTabel';
import SupplierFormDialog from '@/components/admin/supplier/SupplierFormDialog';

export default function SupplierPage() {
    const [suppliers, setSuppliers] = useState<Supplier[]>([]);
    const [loading, setLoading] = useState(true);
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<Supplier | null>(null);

    const [searchQuery, setSearchQuery] = useState('');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    const fetchSuppliers = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/suppliers');
            setSuppliers(response.data);
        } catch (error) {
            toast.error("Gagal memuat data supplier.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchSuppliers(); }, []);

    const filteredSuppliers = suppliers.filter((item) => {
        const searchLower = searchQuery.toLowerCase();
        return item.nama_supplier.toLowerCase().includes(searchLower) ||
               (item.kontak_person && item.kontak_person.toLowerCase().includes(searchLower));
    });

    useEffect(() => { setCurrentPage(1); }, [searchQuery]);
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredSuppliers.slice(indexOfFirstItem, indexOfLastItem);

    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: Supplier) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Yakin hapus supplier ini?")) return;
        try {
            await axios.delete(`/api/suppliers/${id}`);
            toast.success("Supplier dihapus");
            fetchSuppliers();
        } catch {
            toast.error("Gagal menghapus.");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                await axios.put(`/api/suppliers/${editData.id}`, formData);
                
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    await axios.post(`/api/suppliers/${editData.id}/foto`, imgData);
                }
                toast.success("Supplier diperbarui!");
            } else {
                const data = new FormData();
                Object.keys(formData).forEach(key => {
                    if (formData[key] !== null && formData[key] !== '') {
                        data.append(key, formData[key]);
                    }
                });
                await axios.post('/api/suppliers', data);
                toast.success("Supplier ditambahkan!");
            }
            setIsDialogOpen(false);
            fetchSuppliers();
        } catch (error: any) {
            console.error(error);
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        }
    };

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Supplier</h1>
                    <p className="text-gray-500 text-sm">Kelola data pemasok barang dan bahan baku.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Supplier
                </Button>
            </div>

            <SupplierFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
            />

            <SupplierTable 
                loading={loading}
                suppliers={currentItems}
                totalData={filteredSuppliers.length}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            <SupplierFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
            />
        </div>
    );
}
