'use client';

import { useState, useEffect, FormEvent } from 'react';
import { Loader2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import {
    Dialog,
    DialogContent,
    DialogFooter,
    DialogHeader,
    DialogTitle,
} from '@/components/ui/dialog';
import { Supplier } from '@/types';

interface SupplierFormDialogProps {
    isOpen: boolean;
    onOpenChange: (open: boolean) => void;
    onSubmit: (formData: any, isEdit: boolean) => Promise<void>;
    initialData: Supplier | null;
}

export default function SupplierFormDialog({
    isOpen,
    onOpenChange,
    onSubmit,
    initialData,
}: SupplierFormDialogProps) {
    const [isSaving, setIsSaving] = useState(false);
    const [formData, setFormData] = useState({
        nama_supplier: '',
        kontak_person: '',
        nomor_telepon: '',
        email: '',
        alamat: '',
        keterangan: '',
        foto: null as File | null,
    });

    useEffect(() => {
        if (isOpen && initialData) {
            setFormData({
                nama_supplier: initialData.nama_supplier,
                kontak_person: initialData.kontak_person || '',
                nomor_telepon: initialData.nomor_telepon,
                email: initialData.email || '',
                alamat: initialData.alamat || '',
                keterangan: initialData.keterangan || '',
                foto: null
            });
        } else if (isOpen && !initialData) {
            setFormData({
                nama_supplier: '',
                kontak_person: '',
                nomor_telepon: '',
                email: '',
                alamat: '',
                keterangan: '',
                foto: null
            });
        }
    }, [isOpen, initialData]);

    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);
        await onSubmit(formData, !!initialData);
        setIsSaving(false);
    };

    return (
        <Dialog open={isOpen} onOpenChange={onOpenChange}>
            <DialogContent className="sm:max-w-[600px]">
                <DialogHeader>
                    <DialogTitle>{initialData ? 'Edit Supplier' : 'Tambah Supplier Baru'}</DialogTitle>
                </DialogHeader>
                
                <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                    <div className="grid grid-cols-2 gap-4">
                        <div className="space-y-2 col-span-2">
                            <Label>Nama Supplier <span className="text-red-500">*</span></Label>
                            <Input 
                                value={formData.nama_supplier}
                                onChange={(e) => setFormData({...formData, nama_supplier: e.target.value})}
                                required
                            />
                        </div>
                        <div className="space-y-2">
                            <Label>Kontak Person</Label>
                            <Input 
                                placeholder="Cth: Pak Budi"
                                value={formData.kontak_person}
                                onChange={(e) => setFormData({...formData, kontak_person: e.target.value})}
                            />
                        </div>
                        <div className="space-y-2">
                            <Label>Nomor Telepon <span className="text-red-500">*</span></Label>
                            <Input 
                                placeholder="Cth: 0812..."
                                value={formData.nomor_telepon}
                                onChange={(e) => setFormData({...formData, nomor_telepon: e.target.value})}
                                required
                            />
                        </div>
                        <div className="space-y-2 col-span-2">
                            <Label>Email</Label>
                            <Input 
                                type="email"
                                placeholder="email@supplier.com"
                                value={formData.email}
                                onChange={(e) => setFormData({...formData, email: e.target.value})}
                            />
                        </div>
                    </div>

                    <div className="space-y-2">
                        <Label>Alamat</Label>
                        <Textarea 
                            value={formData.alamat}
                            onChange={(e) => setFormData({...formData, alamat: e.target.value})}
                        />
                    </div>
                    
                    <div className="space-y-2">
                        <Label>Keterangan</Label>
                        <Input 
                            placeholder="Cth: Supplier Beras & Telur"
                            value={formData.keterangan}
                            onChange={(e) => setFormData({...formData, keterangan: e.target.value})}
                        />
                    </div>

                    <div className="space-y-2">
                        <Label>Logo Supplier</Label>
                        <Input 
                            type="file" 
                            accept="image/*"
                            className="cursor-pointer"
                            onChange={(e) => {
                                if (e.target.files?.[0]) setFormData({ ...formData, foto: e.target.files[0] });
                            }}
                        />
                    </div>

                    <DialogFooter className="pt-4">
                        <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>Batal</Button>
                        <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                            {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />} Simpan
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog>
    );
}